<template>
  <div v-show="isShow">
    <slot></slot>
  </div>
</template>

<script>

export default {
  name: "AnimateGroup",
  props: {
    name: {
      type: String,
      required: false
    },
    animateDelay: {
      type: Number,
      required: false,
      default: .3
    },
    groupDelay: {
      type: Number,
      required: false,
      default: .8
    },
  },

  data() {
    return {
      isShow: false,
    };
  },
  watch: {
    "$animatePlugin.orderGroupName": {
      handler(orderGroupName) {
        if (orderGroupName === this.name) {
          this.showAnimateBox();
        } else {
          this.hideAnimateBox();
        }
      },
      immediate: true,
    },
  },
  methods: {
    showAnimateBox() {
      this.isShow = true;

      setTimeout(
        () => {
          this.$children.forEach((e, i) => {
            setTimeout(() => {
              e.show();
            }, this.animateDelay * i * 1000);
          });
          this.$animatePlugin.currentGroupName = this.name;
        },
        this.$animatePlugin.currentGroupName ? this.groupDelay * 1000 : 0
      );
    },
    hideAnimateBox() {
      this.$nextTick(() => {
        Promise.all(
          this.$children.map((c, i) => c.hide(this.animateDelay * i * 1000))
        ).then((values) => {
          this.isShow = false;
        });
      });
    },
  },
};
</script>

